.jumbotron {
  background-color: rgb(78, 129, 151);
  color: white;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 15px 0 rgba(0, 0, 0, 0.2);
}

.navbar {
  background-color: rgb(12, 44, 58);
}

.navbar-brand, .nav-link {
  color: white;
}

.nav-link:hover {
  color: lightgrey;
}

#score {
  margin-top: 40px;
}
.game-card {
  width: 100px;
  height: 100px;
  border-radius: 5px;
  background-color: white;
  border: 1px rgb(190, 190, 190) solid;
  margin: 5px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  box-shadow: 0 4px 8px 0 grey ;
}

.game-img {
  max-width: 100%;
  max-height: 100%;
  padding: 5px;
}

@media (min-width: 800px) {
  .game-card {
    width: 180px;
    height: 180px;
    margin: 15px;
  }
  
  .game-img {
    padding: 10px;
  }

  .game-card:hover {
    cursor: pointer;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    box-shadow: 0 4px 8px 0 grey;
  }
}


body {
  font-family: Verdana, Geneva, sans-serif;
}
